.login-container{
    display: flex;
    flex: 1;
    width: 100vw;
    min-height: 120vh;
}

.form-container{
    flex: 1;
    padding: 50px;
    margin: 10px;
    align-content: center;
}

.form-box{
    justify-content: left;
    align-items: inherit;
    grid-row: 50%;
    margin-top: -50px;
}

.image-container{
    color: skyblue;
    max-width: 50%;
}

.input-form{
    width: 20px;
}

.heading,
h1{
  font-size: 50px;
}

.heading1,
h3{
    font-size: 25px;
    margin-top: 20px;
}

.image-container{
    flex: 2;
    margin-left: -100px;
}

.image{
    width: 100%;
    height: 80%;
    /* margin-top: 10px; */

}

.imga{
    justify-content: center;
    text-align: center;
}

.input{
    display: grid;
    margin-bottom: 1px;
    padding: 10px;
    background-color: white;
    margin-top: 50px;
}

.email,
.password{
    background-color: azure;
    width: 70%;
    border-radius: 8px;
    margin-top: 20px;
    padding: 14px;
    border: none;
    outline: none;

}

.display-name{
    background-color: azure;
    width: 70%;
    border-radius: 8px;
    padding: 14px;
    border: none;
    outline: none;
    margin-top: 20px;
}

.btn-login{
    padding: 7px;
}

.btn{
    background-color: rgb(92, 199, 243);
    width: 70%;
    /* margin-right: 15px; */
    padding: 14px;
    border-radius: 8px;
    font-size: 17px;
    border: none;
    outline: none;
    margin-bottom: 20px;
    margin-top: 15px;
}

.g.btn{
  border: 1px solid;
  height: 40px !important;
  line-height: 40px !important;
  font-size: 15px !important;
  width: 70% !important;
  margin: 10px;
}

.google-button{
    margin-top: 5%;
    margin-left: 27%;
    margin-bottom: 15px;
}

.g.btn>div,
.g.btn>div>svg{
    font-size: large;
}

.errorMessage{
    color: red;
    font-weight: 500;
    margin: 10px 0;
}

.signUpText>Link{
    text-decoration: none;
    color: var(--twitter-color);
}

@media screen and (max-width: 688px){
    .login-container{
        order: 2;
        display: flex;
        flex-direction: column;
        font-size: 20px;
    }

    .form-container{
        padding: 10px;
    }

    .image-container{
        width: 100px;
        vertical-align: text-bottom;
        display: flex;
        flex-direction: column-reverse;
        order: 1;
        max-width: 100%;
        width: 100%;
    }

    .image{
        order: 2;
        width: 100%;
        height: 100%;
        margin-left: 100%;
    }
}
    
